<template>
  <header>
    <div class="l-content">
      <el-button
        @click="handleMenu"
        plain
        icon="el-icon-menu"
        size="mini"
      ></el-button>
      <!--el-Breadcrumb面包屑组件,数据定义在tab中-->
      <el-breadcrumb separator-class="el-icon-arrow-right" class="myColor">
        <el-breadcrumb-item
          v-for="item in tags"
          :key="item.path"
          :to="{ path: item.path }"
          >{{ item.label }}</el-breadcrumb-item
        >
      </el-breadcrumb>
    </div>
    <div class="r-content">
      <el-dropdown trigger="click" size="mini"
        ><!--控制下拉菜单-->
        <span>
          <!--头像-->
          <img class="user" :src="userImg" alt="" />
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>个人中心</el-dropdown-item>
          <el-dropdown-item @click.native="logout">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </header>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "CommonHeader",
  data() {
    return {
      userImg: require("../assets/userImg/userImg.jpg"),
    };
  },
  methods: {
    handleMenu() {
      //控制侧边栏伸缩
      this.$store.commit("CollapseMenu");
    },
    logout() {
      //登出
      this.$store.commit("clearToken");
      this.$store.commit("clearMenu");
      this.$router.push("/login");
    },
  },
  computed: {
    ...mapState({
      tags: (state) => state.tab.tabsList, //拿到tab面包屑数据
    }),
  },
};
</script>

<style lang="css" scoped>
header {
  display: flex;
  height: 100%;
  justify-content: space-between; /*贴边 */
  align-items: center; /*图标上下居中*/
}
.l-content {
  display: flex;
  align-items: center;
}
.l-content .el-button {
  margin-right: 20px;
}
.user {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
/**面包屑样式 */
.myColor /deep/ .el-breadcrumb__inner {
  color: skyblue;
  font-weight: normal;
}
</style>
